.card-options {
  margin: 0;
  grid-column: 2 /3;
}

.card-options__weight {
  @include catalog-card-options;
  text-align: left;
  padding: 0;
}

.card-options__value {
  @include catalog-card-options;
  text-align: right;
  padding: 0;
}

.card-options__taste {
  @include catalog-card-options;
}

.card-options__price {
  @include catalog-card-options;
}

@media (min-width: $tablet-width) {
  .card-options__weight {
    @include catalog-card-options-tablet;
    width: 120px;
  }

  .card-options__value {
    @include catalog-card-options-tablet;
    width: 120px;
  }

  .card-options__taste {
    @include catalog-card-options-tablet;
  }

  .card-options__price {
    @include catalog-card-options-tablet;
  }
}

@media (min-width: $desktop-width) {
  .card-options__weight {
    width: 84px;
  }

  .card-options__value {
    width: 84px;
  }
}
